.App-link {
  color: var(--link-color);
}

.d-none {
  display: none;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.sub-text {
  max-width: 50%;
  text-align: justify;
  color: var(--secondary-font-color);
}

.mx-1 {
  margin-left: 1.5rem;
  margin-right: 1.5rem;
}

.px-1 {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.my-1 {
  margin-left: 1.5rem;
  margin-right: 1.5rem;
}

.py-1 {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}

h1 {
  font-size: 5rem;
  margin-bottom: 0;
  text-align: left;
}

h2 {
  font-size: 3rem;
  margin-bottom: 0;
}

.info-text {
  color: var(--link-color);
}

.card-row {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
}

.card-row a {
  text-decoration: none;
  text-align: start;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
  width: 340px;
  margin: 1rem 0;
  padding: 1rem 1rem 1.8rem 1rem;
  border-radius: 32px;
  background-color: var(--secondary-bg-color);
  color: white;
  min-height: 425px;
}

.card img {
  width: 100%;
  border-radius: 32px;
  max-height: 200px;
  filter: brightness(84%) saturate(132%) contrast(103%);
  -webkit-filter: brightness(84%) saturate(132%) contrast(103%);
  -moz-filter: brightness(84%) saturate(132%) contrast(103%);
}

.card a {
  width: 100%;
  border-radius: 32px;
  max-height: 200px;
  font-style: normal;
  text-decoration: none;
}

.card .card-heading {
  font-size: 1.3rem;
  line-height: 1.6;
  font-weight: bold;
  margin-bottom: 0;
  text-align: left;
}

.card .card-details,
.card .read-more {
  font-size: 1rem;
  line-height: 1.6;
}

.card button .km-text:hover {
  text-decoration: underline;
}
.card button {
  font-weight: 800;
  font-family: "Josefin Sans", "sans-serif";
  color: #fff;
  cursor: pointer;
  background: var(--link-color);
  border: none;
  border-radius: 15px;
  padding: 0.5rem 1rem;
}

.card button:disabled {
  background: #ccc;
  color: #fff;
}

.g-text:hover {
  background: rgb(63, 94, 251);
  background: linear-gradient(
    90deg,
    rgba(63, 94, 251, 1) 0%,
    rgba(252, 70, 107, 1) 100%
  );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: background-position 0.4s, color 0.4s;
  background-position: 100% 50%;
  text-decoration: none;
}

.flag {
  background-image: linear-gradient(
    0deg,
    #138808 33%,
    #ffffff 33%,
    #ffffff 66%,
    #ff9933 66%
  );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-position: 100% 50%;
  text-decoration: none;
}

@media screen and (max-width: 768px) {
  .sub-text {
    max-width: 100%;
    text-align: left;
  }
  .card-row {
    flex-flow: row nowrap;
    overflow-x: auto;
    padding: 1rem;
    margin-left: -1rem;
  }
  .card {
    margin: 0 1rem 0 0;
    max-width: 60vw;
  }
}

